<template>
  <div class="container">
    <div class="form-group">
      <label for="qnaTitle">제목</label>
      <input type="text" class="form-control" id="qnaTitle" ref="qnaTitle" placeholder="제목을 입력하세요" v-model="qnaTitle" />
    </div>
    <div class="form-group">
      <label for="qnaContent">내용</label>
      <textarea rows="10" style="resize:none;" type="text" class="form-control" id="qnaContent" ref="qnaContent" placeholder="내용을 입력하세요" v-model="qnaContent"></textarea>
    </div>
    <div class="text-right">
      <button class="btn btn-light" @click="checkHandler">등록</button>
      <button class="btn btn-light" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Create",
  data: function() {
    return {
      qnaNo: "",
      qnaDatetime: "",
      qnaUserid: "",
      qnaTitle: "",
      qnaContent: "",
    };
  },
  created() {
    axios.get("http://localhost:8080/api/session").then(({ data }) => {
      this.qnaUserid = data.id;
      console.log(this.qnaUserid);
    });
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.qnaTitle && ((msg = "제목 입력해주세요"), (err = false), this.$refs.qnaTitle.focus());
      err && !this.qnaContent && ((msg = "내용 입력해주세요"), (err = false), this.$refs.qnaContent.focus());

      if (!err) alert(msg);
      else this.createHandler();
    },
    createHandler() {
      axios
        .post("http://localhost:9999/happyhouse/api/qna", {
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
        })
        .then(({ data }) => {
          let msg = "등록 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "등록이 완료되었습니다.";
          }
          alert(msg);
          this.moveList();
        });
    },
    moveList() {
      this.$router.push("/QnA");
    },
  },
};
</script>

