import Vue from "vue";
import VueRouter from "vue-router";
import List from "@/components/List.vue";
import Create from "@/components/Create.vue";
import Read from "@/components/Read.vue";
import Update from "@/components/Update.vue";
import Delete from "@/components/Delete.vue";

Vue.use(VueRouter);

export default new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/QnA",
      component: List,
    },
    {
      path: "list",
      name: "list",
      component: List,
    },
    {
      path: "create",
      name: "create",
      component: Create,
    },
    {
      path: "read",
      name: "read",
      component: Read,
    },
    {
      path: "update",
      name: "update",
      component: Update,
    },
    {
      path: "delete",
      name: "delete",
      component: Delete,
    },
  ],
});
