<template>
  <div class="container">
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{ width: '5%' }" />
          <col :style="{ width: '50%' }" />
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '25%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        <tr v-for="(item, index) in items" :key="index + '_items'">
          <td>{{ item.qnaNo }}</td>
          <td>
            <router-link :to="{ name: 'read', query: { qnaNo: item.qnaNo } }">{{ item.qnaTitle }}</router-link>
          </td>
          <td>{{ item.qnaUserid }}</td>
          <td>{{ getFormatDate(item.qnaDatetime) }}</td>
        </tr>
      </table>
    </div>
    <div v-else class="text-center container" style="padding:40px">작성 된 글이 없습니다.</div>
    <div class="text-center">
      <select v-model="searchBy" id="searchBy">
        <option value="0">글번호</option>
        <option value="1">글제목</option>
        <option value="2">작성자</option>
      </select>
      <input type="text" id="keyword" v-model="keyword" @keyup.enter="showList(keyword)" />
      <button class="btn btn-light" @click="showList(keyword)">검색</button>
      <router-link v-if="userid" style="float:right;" :to="{ name: 'create' }">글쓰기</router-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";

export default {
  name: "List",
  data: function() {
    return {
      searchBy: 0,
      keyword: "",
      items: [],
      userid: "",
    };
  },
  created() {
    axios.get("http://localhost:9999/happyhouse/api/qna").then(({ data }) => {
      console.log(data);
      this.items = data;
    });

    axios.get("http://localhost:8080/api/session").then(({ data }) => {
      this.userid = data.id;
    });
  },

  methods: {
    showList(keyword) {
      let temp = {
        items: [],
      };
      axios({
        methods: "get",
        url: "http://localhost:9999/happyhouse/api/qna",
      })
        .then((response) => {
          temp.items = response.data;

          if (this.searchBy == 0) {
            if (keyword) {
              this.items = temp.items.filter((item) => {
                return item.qnaNo == parseInt(keyword);
              });
            } else {
              this.items = temp.items;
            }
          } else if (this.searchBy == 1) {
            if (keyword) {
              this.items = temp.items.filter((item) => {
                return item.qnaTitle.includes(keyword);
              });
            } else {
              this.items = temp.items;
            }
          } else {
            if (keyword) {
              this.items = temp.items.filter((item) => {
                return item.qnaUserid.includes(keyword);
              });
            } else {
              this.items = temp.items;
            }
          }
        })
        .catch((error) => {
          console.log(error);
        })
        .finally(() => {});
    },
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD");
    },
    movePage() {
      this.$router.push("/create");
    },
  },
};
</script>

<style></style>
