<template>
  <div class="container">
    <table class="table table-bordered">
      <tr>
        <th>번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.qnaDatetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">{{ item.qnaContent }}</td>
      </tr>
    </table>

    <div class="text-right" v-if="userId === item.qnaUserid">
      <router-link :to="{ name: 'list' }">
        <span class="my-btn">목록</span>
      </router-link>
      <router-link :to="{ name: 'update', query: { qnaNo: item.qnaNo } }">
        <span class="my-btn">수정</span>
      </router-link>
      <router-link :to="{ name: 'delete', query: { qnaNo: item.qnaNo } }">
        <span class="my-btn">삭제</span>
      </router-link>
    </div>

    <div class="container" v-if="replys.length">
      <table class="table table-condensed">
        <colgroup>
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '60%' }" />
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '5%' }" />
          <col :style="{ width: '5%' }" />
        </colgroup>

        <tr v-for="(reply, index) in replys" :key="index + '_replys'">
          <td>{{ reply.replyUserid }}</td>
          <td v-if="ModifyButtonClicked && index === modifyIndex">
            <textarea class="form-control" style="resize:none;" id="modifyContent" placeholder="내용을 입력하세요" v-model="modifyContent"></textarea>
          </td>
          <td v-else>{{ reply.replyContent }}</td>
          <td class="text-muted">{{ getFormatDate(reply.replyDatetime) }}</td>
          <td v-if="ModifyButtonClicked && index === modifyIndex">
            <span class="my-btn" @click="ReplyUpdateHandler(reply.replyNo)">저장</span>
          </td>
          <td v-else>
            <span class="my-btn" v-if="reply.replyUserid === userId" @click="updateButtonClick(index)">수정</span>
          </td>
          <td v-if="ModifyButtonClicked && index === modifyIndex">
            <span class="my-btn" @click="cancelButtonClick()">취소</span>
          </td>
          <td v-else>
            <span class="my-btn" v-if="reply.replyUserid === userId" @click="ReplyDeleteHandler(reply.replyNo)">삭제</span>
          </td>
        </tr>
      </table>
    </div>
    <br />
    <div>
      <div class="row">
        <div class="col-sm-1"></div>
        <textarea class="form-control col-sm-9" style="resize:none;" id="replyContent" placeholder="내용을 입력하세요" v-model="replyContent"></textarea>
        <button class="btn btn-light col-sm-1" @click="ReplyCreateHandler">등록</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "read",
  data: function() {
    return {
      modifyContent: "",
      ModifyButtonClicked: false,
      modifyIndex: -1,
      replyUserid: "",
      userId: "",
      replyContent: "",
      boardNo: 0,
      item: {},
      replys: {},
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    this.boardNo = params.get("qnaNo");
    axios.get(`http://localhost:9999/happyhouse/api/qna/${params.get("qnaNo")}`).then(({ data }) => {
      console.log(data);
      this.item = data;
    });

    this.getReplyData();
    axios.get("http://localhost:8080/api/session").then(({ data }) => {
      console.log(data);
      this.userId = data.id;
    });
  },
  methods: {
    getReplyData() {
      axios.get(`http://localhost:9999/happyhouse/api/reply/${this.boardNo}`).then(({ data }) => {
        console.log(data);
        this.replys = data;
      });
    },
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    },
    ReplyCreateHandler() {
      axios
        .post("http://localhost:9999/happyhouse/api/reply", {
          boardNo: this.item.qnaNo,
          replyUserid: this.userId,
          replyContent: this.replyContent,
        })
        .then(({ data }) => {
          let msg = "등록 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "등록이 완료되었습니다.";
          }
          alert(msg);
          this.getReplyData();
          this.replyContent = "";
        });
    },
    ReplyDeleteHandler(replyNo) {
      console.log(`http://localhost:9999/happyhouse/api/reply/${this.item.qnaNo}/replyNo`);
      axios.delete(`http://localhost:9999/happyhouse/api/reply/${this.item.qnaNo}/${replyNo}`).then(({ data }) => {
        let msg = "삭제 처리시 문제가 발생했습니다.";
        if (data === "success") {
          msg = "삭제가 완료되었습니다.";
        }
        alert(msg);
        this.getReplyData();
      });
    },
    ReplyUpdateHandler(replyNo) {
      axios
        .put(`http://localhost:9999/happyhouse/api/reply/${this.item.qnaNo}/${replyNo}`, {
          replyNo: replyNo,
          boardNo: this.item.qnaNo,
          replyContent: this.modifyContent,
          replyUserid: this.userId,
          replyDatetime: new Date(),
        })
        .then(({ data }) => {
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
          }
          alert(msg);
          this.getReplyData();
          this.ModifyButtonClicked = false;
          this.modifyContent = "";
        });
    },
    updateButtonClick(index) {
      this.modifyIndex = index;
      this.ModifyButtonClicked = true;
    },
    cancelButtonClick() {
      this.ModifyButtonClicked = false;
    },
  },
};
</script>

<style scoped>
.my-btn {
  font-size: xx-small;
  padding-left: 3px;
  color: #a6a6a6;
}
.my-btn:hover {
  text-decoration: underline;
}
a {
  color: #a6a6a6;
}
span:hover {
  cursor: pointer;
}
</style>
